</slot>\n                <button type=\"button\" @click=\"close\">Cancel</button>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\n    import Closable from './../Mixins/Closable';\n\n    export default {\n        mixins: [Closable],\n\n        props: {\n            title: {\n                type: String,\n                required: true,\n            },\n        },\n    }\n</script>\n\n<style scoped>\n.hidden {\n  display: none;\n}\n\n[role=\"alertdialog\"],\n[role=\"dialog\"] {\n  box-sizing: border-box;\n  padding: 15px;\n  border: 1px solid #000;\n  background-color: #fff;\n  min-height: 100vh;\n}\n\n@media screen and (min-width: 640px) {\n  [role=\"alertdialog\"],\n  [role=\"dialog\"] {\n    position: absolute;\n    top: 2rem;\n    left: 50vw; /* move to the middle of the screen (assumes relative parent is the body/viewport) */\n    transform: translateX(\n      -50%\n    ); /* move backwards 50% of this element's width */\n\n    min-width: calc(640px - (15px * 2)); /* == breakpoint - left+right margin */\n    min-height: auto;\n    box-shadow: 0 19px 38px rgb(0 0 0 / 12%), 0 15px 12px rgb(0 0 0 / 22%);\n  }\n}\n\n.dialog_label {\n  text-align: center;\n}\n\n.dialog_form {\n  margin: 15px;\n}\n\n.dialog_form .label_text {\n  box-sizing: border-box;\n  padding-right: 0.5em;\n  display: inline-block;\n  font-size: 16px;\n  font-weight: bold;\n  width: 30%;\n  text-align: right;\n}\n\n.dialog_form .label_info {\n  box-sizing: border-box;\n  padding-right: 0.5em;\n  font-size: 12px;\n  width: 30%;\n  text-align: right;\n  display: inline-block;\n}\n\n.dialog_form_item {\n  margin: 10px 0;\n  font-size: 0;\n}\n\n.dialog_form_item .wide_input {\n  box-sizing: border-box;\n  max-width: 70%;\n  width: 27em;\n}\n\n.dialog_form_item .city_input {\n  box-sizing: border-box;\n  max-width: 70%;\n  width: 17em;\n}\n\n.dialog_form_item .state_input {\n  box-sizing: border-box;\n  max-width: 70%;\n  width: 15em;\n}\n\n.dialog_form_item .zip_input {\n  box-sizing: border-box;\n  max-width: 70%;\n  width: 9em;\n}\n\n.dialog_form_actions {\n  text-align: right;\n  padding: 0 20px 20px;\n}\n\n.dialog_close_button {\n  float: right;\n  position: absolute;\n  top: 10px;\n  left: 92%;\n  height: 25px;\n}\n\n.dialog_close_button img {\n  border: 0;\n}\n\n.dialog_desc {\n  padding: 10px 20px;\n}\n\n/* native <dialog> element uses the ::backdrop pseudo-element */\n\n/* dialog::backdrop, */\n.dialog-backdrop {\n  display: none;\n  position: fixed;\n  overflow-y: auto;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n}\n\n@media screen and (min-width: 640px) {\n  .dialog-backdrop {\n    background: rgb(0 0 0 / 30%);\n  }\n}\n\n.dialog-backdrop.active {\n  display: block;\n}\n\n.no-scroll {\n  overflow-y: auto !important;\n}\n\n/* this is added to the body when a dialog is open */\n.has-dialog {\n  overflow: hidden;\n}\n\n/* styling for alert-dialog example */\n.notes {\n  display: block;\n  font-size: 1rem;\n  line-height: 1.3;\n  min-width: 400px;\n  max-width: 100%;\n  width: 33%;\n}\n\n.toast {\n  background-color: rgb(0 0 0 / 90%);\n  color: #fff;\n  padding: 1rem;\n  border: none;\n  border-radius: 0.25rem;\n  box-shadow: 0 3px 6px rgb(0 0 0 / 16%), 0 3px 6px rgb(0 0 0 / 23%);\n  position: fixed;\n  top: 1rem;\n  right: 1rem;\n  transform: translateY(-150%);\n  transition: transform 225ms cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.toast.active {\n  transform: translateY(0);\n}\n</style>\n"],"sourceRoot":""}]);
